"use client";

import { useState } from "react";
import PackageCard from "../../components/PackageCard";
import PackageSearch from "../../components/PackageSearch";
import { tourPackages, TourPackage } from "../../data/tourPackages";

export default function PackagesPage() {
  const [filteredPackages, setFilteredPackages] = useState<TourPackage[]>(tourPackages);

  const handleSearch = (filters: {
    destination?: string;
    departureDate?: string;
    priceRange?: [number, number];
  }) => {
    const results = tourPackages.filter((pkg) => {
      const matchDestination = filters.destination ? pkg.destination.toLowerCase().includes(filters.destination.toLowerCase()) : true;
      const matchDeparture = filters.departureDate ? pkg.departureDate === filters.departureDate : true;
      const matchPrice =
        filters.priceRange
          ? pkg.price >= filters.priceRange[0] && pkg.price <= filters.priceRange[1]
          : true;

      return matchDestination && matchDeparture && matchPrice;
    });

    setFilteredPackages(results);
  };

  return (
    <main className="max-w-7xl mx-auto px-6 py-16 space-y-10">
      <h1 className="text-4xl font-extrabold text-sky-700 text-center">
        Our Tour Packages
      </h1>

      {/* Search Component */}
      <PackageSearch onSearch={handleSearch} />

      {/* Packages Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8">
        {filteredPackages.length > 0 ? (
          filteredPackages.map((pkg) => <PackageCard key={pkg.id} pkg={pkg} />)
        ) : (
          <p className="text-center text-gray-500 col-span-full">
            No packages match your search.
          </p>
        )}
      </div>
    </main>
  );
}
